import * as React from 'react';
import { Block, RenderNodeProps } from '@ali/4ever-cangjie';
import CodeEditor from './components/CodeEditor';
import { CodePluginConfig } from './types';
import { CodeWrapper } from '../utils/constants/themes';

export default function createRenderBlock(
  config: CodePluginConfig,
) {
  return {
    code: (props: RenderNodeProps, controller) => {
      const { attributes, isSelected } = props;
      const node = props.node as Block;

      const { cursorPos = 'end' } = node.data;
      return (
        <CodeWrapper
          {...attributes}
          data-testid="code-editor"
          data-type="code"
        >
          <CodeEditor
            cursorPos={cursorPos}
            onSave={config.onSave}
            isSelected={isSelected}
            controller={controller}
            node={node}
            locale={config.locale}
          />
        </CodeWrapper>
      );
    },
  };
}
